@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';


























































































.pageTemplateTable {
  .table-wrap .el-table__header,
  .table-wrap .el-table__body,
  .table-wrap .el-table__footer {
    border-left: none !important;
  }
  .el-table__header {
    width: 100%;
  }
  .el-table--small th {
    padding: 0;
  }
  .el-table {
    border: none;
  }
  // .table-wrap .table .table_head th {
  //   background-color: #f5f6fa !important;
  // }
  .el-table tr td,
  .el-table .table_head th {
    border-right: 1px solid #ebecf0;
    &:first-child {
      border-left: 1px solid #ebecf0;
    }
  }
}
