@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';

























































































































































































.template-box {
  height: 100%;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 4px;
  }
  .echartsBox {
    padding-left: 15px;
    padding-bottom: 15px;
    box-sizing: border-box;
  }
  .tableBox {
    .base-table .cell,
    .base-table .table-wrap .table .table_head th .cell {
      font-size: 12px;
      padding-left: 5px;
      padding-right: 5px;
    }
  }
  .simple-card {
    margin-bottom: 0;
    height: 100%;
    .simple-card-body {
      height: calc(100% - 30px);
    }
  }
  .block-param-form {
    &.echartsBoxForm {
      padding-left: 15px;
    }
    .el-form-item__content,
    .el-date-editor.el-input,
    .el-date-editor.el-input__inner {
      width: 140px;
    }
    .el-form-item {
      margin-bottom: 10px;
    }
    &.minWidth .el-form-item__label {
      min-width: 72px;
    }
  }
}
