@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';
















































































.monitorChart {
  position: relative;
  height: 60%;
  border: 1px solid;
  padding: 10px 20px;
  z-index: 100;
  .info {
    .title {
      height: 20px;
      font-size: 14px;
      margin-bottom: 0;
    }
    .ratio {
      margin: 0;
      height: 30px;
      padding-top: 10px;
      span:first-child {
        display: inline-block;
        width: 60px;
      }
      .line {
        width: 10px;
        text-align: center;
      }
      span:first-child,
      span:last-child {
        // border: 1px dashed #ddd;
      }
    }
  }
  .imgStyle {
    position: absolute;
    top: 8%;
    right: 5%;
  }
  .progress {
    position: absolute;
    bottom: 5%;
    left: 20px;
    right: 20px;
    color: #666;
  }
}
