@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';















































































































































































































































































.preivew-grid-layout {
  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  .grid-item {
    box-shadow: 1px 1px 8px 0 rgba(15, 30, 66, 0.1);
    border-radius: 2px;
    .template-box {
      background: $color-back-card;
      box-sizing: border-box;
      padding: 10px;
      &::-webkit-scrollbar {
        width: 4px;
      }
    }
  }
}
