@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';




















































































.monitorSteps {
  padding: 0 10px 0;
  // display: flex;
  display: -webkit-box;
  justify-content: center;
  font-size: 14px;
  height: 100%;
  .stepItem {
    width: 50%;
    max-width: 150px;
    text-align: center;
    min-width: 110px;
    .itemInfo {
      .stepName {
        font-size: 16px;
      }
      .allCount {
        height: 15px;
        overflow: hidden;
      }
      .progress {
        width: 100%;
        height: 40px;
        position: relative;
        margin: 20px 0;
        .el-progress {
          z-index: 1000;
          background: #fff;
          .el-progress-circle {
            width: 40px !important;
            height: 40px !important;
            margin: 0 auto;
          }
        }
        .itemLine {
          width: 80%;
          height: 3px;
          background: #2464b3;
          position: absolute;
          top: 48%;
          left: 60%;
        }
      }
      .uncomplated,
      .complatedStep {
        padding-left: 25px;
        text-align: left;
        width: 100%;
      }
      .uncomplated {
        span:last-child {
          color: rgb(234, 169, 38);
        }
      }
      .complatedStep {
        span:last-child {
          color: rgb(68, 179, 101);
        }
      }
    }
  }
}
